body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html, body, #root {
  width: 100%;
  height: 100%;
}
.App {
    width: 100%;
    height: 100%;
}

.App_body {
    width: 100%;
    height: 100%;
}

.App_body_loading{
  width: 100%;
  height: 100%;
  display: inline-flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  flex-wrap: wrap;
}

.navigation {

    width: 100%;
    min-height: 5%;

    display: inline-block;
    text-align: center;
}

.body {

    width: 100%;
    height: 60%;

    display: inline-table;
}

.body_trend {
    width: 20%;
    height: 100%;
    overflow-y: scroll;

    float: left;

    margin: 0 20px 0 20px;
    padding : 10px 20px 10px 20px;
}

.body_context {
    background-color: rgb(126, 121, 121);

    width: 70%;
    max-width: 70%;
    height: 100%;
    overflow-y: scroll;

    float: left;

    margin: 0 20px 0 20px;
    padding : 10px 20px 10px 20px;
}
.navigtion_origin {
    margin: 10px;
    padding: 10px;
}

.select_Type{
    width: 100px;
    height: 55px;
    font-size: 20px;
    margin-right: 10px;
}

.contentArea {
    margin : 10px;
}
.RankingDiv {
    margin: 10px
}
.chart {
    overflow: hidden;
}
